<template>
  <div>
    <Tab1></Tab1>
    <div class="flowBox clearfix" id="flowBox">
    <div data-height="0"  v-for="(item,index) in picAry" :key="index" id="box">
      <div class="picBox"><img :src="item.pic" alt=""></div>
      <h3 id="span-name">{{item.title}}</h3>
  </div>
    </div>
  </div>
</template>
<script>
import Tab1 from '../components/Tab1.vue'
import { getpull } from '../model/sliders'
export default {
  name: 'du',
  data () {
    return {
      picAry: []
    }
  },
  async created () {
    await getpull().then((data) => {
      this.picAry = data.data
    })
  },
  components: {
    Tab1
  }
}
</script>
<style scoped>
div{
  overflow: hidden;
}
.flowBox {
  /* width: 100%; */
}
.flowBox img {
  width: 100%;
}
.picBox{
  width: 100%;
}
#span-name{
  font-size: .25rem;
  text-align: center;
}
#box{
  width: 50%;
  float: left;
}
</style>
