<template>
  <div>
    <Header></Header>
    <div class="content">
      <swiper :sliders="slidesAry"></swiper>
    </div>
    <div class="content">
      <ul>
        <li v-for="(item,index) in buyList" :key="index">
          <img :src="item.buyImg" alt id="img" />
          <h2>{{item.name}}</h2>
          <p>{{item.price}}</p>
          <i class="el-icon-star-off" id="icon-collect" @click="collect(item.id)"></i>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import Header from "../components/Header.vue";
import swiper from "../components/swiper.vue";
import { getSliders, getBuyList, collectList } from "../model/sliders"
export default {
  name: "buy",
  data() {
    return {
      slidesAry: [],
      buyList: [],
      massage: {
        pic: "",
        title: "",
        price: ""
      }
    };
  },
  created() {
    getSliders().then(config => {
      this.slidesAry = config.data;
    });
    getBuyList().then(data => {
      this.buyList = data.data;
    });
  },
  methods: {
    async collect(id) {
      let result = await collectList(id);
      console.log(result);
      if (result.data.code == 0) {
        this.$store.commit("add")
      }
    }
  },
  components: {
    Header,
    swiper
  }
};
</script>
