import axios from 'axios'
// 请求轮播图片
export let getSliders = () => axios.get('/api/sliders')
// 请求购买页列表
export let getBuyList = () => axios.get('/api/getBuy')
// 瀑布流图片
export let getpull = () => axios.get('/api/getpull')
// 收藏列表
export let collectList = id => axios.get('/api/collectList?id='+ id)
// 渲染收藏列表 
export let getCollectList = () => axios.get('/api/getCollectList')
// 校验登录密码：
export let checkM = body => axios.post("/api/getCheckM",body)