<template>
  <div id="du-img">
    <img src="../assets/home3.png" alt="" >
    <div id="design-span">
      <span >用户名：</span><input type="text" v-model="data.userName">
    </div>
    <div id="design-span1">
      <span >密&nbsp;&nbsp;&nbsp;码：</span><input type="text" v-model="data.pwd">
    </div>
    <button id="design-btn" @click="checkMsg">登录</button>
  </div>
</template>
<script>
import { checkM } from '../model/sliders'
export default {
  data () {
    return {
      data: {
        userName: '',
        pwd: ''
      }
    }
  },
  methods: {
    async checkMsg () {
      let result = await checkM(this.data)
      if(result.data.code === 0) {
        localStorage.setItem('isLogin', 'ok')
        alert('登录成功')
        this.$router.push('/myHome')
      } else {
        alert('用户名或密码错误')
      }
    }
  }
}
</script>
<style>
#du-img{
    width: 100%
}
img{
    width: 100%
}
#design-span{
  color:white;
  position: absolute;
  top:5rem;
  font-size: .4rem;
  left: 1.5rem;
}
#design-span1{
  color:white;
  position: absolute;
  top:6rem;
  font-size: .4rem;
  left: 1.5rem;
}
input{
    width: 3rem;
    height: .4rem;
    font-size: .4rem
}
#design-btn{
  position: absolute;
  top:7rem;
  left: 3.5rem;
  width: 1rem;
  height: .5rem;
  font-size: .25rem;
}
</style>
