<template>
  <div>
    <el-autocomplete
  v-model="state"
  :fetch-suggestions="querySearchAsync"
  placeholder="请输入内容"
  @select="handleSelect"
  class="input"
    ></el-autocomplete>
    <i class="el-icon-search" id="search"></i>
    </div>
</template>
<script>
export default {
  data () {
    return {
      restaurants: [],
      state: '',
      timeout: null
    }
  },
  methods: {
    loadAll () {
      return [
        { 'value': 'Nike Kyrie 5  UFO 太空船' },
        { 'value': 'Puma Thunder Speactra 泫雅联名' },
        { 'value': 'Air Jordan 7' }
      ]
    },
    querySearchAsync (queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect (item) {
      // console.log(item)
    }
  },
  mounted () {
    this.restaurants = this.loadAll()
  }
}
</script>
<style lang="less" >
  .input{
    width: 6rem;
    margin-left: .3rem;
  }
  #search{
   font-size: .5rem;
   margin-top: -0.5rem!important;
  }
</style>
