import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    num: 0,
    flag: false
  },
  mutations: {
    add (state) {
      state.num++
      if(state.num > 0) {
        state.flag = true
      }
    },
    remove (state) {
      state.flag = false
    }
  },
  actions: {

  }
})
